.chat-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.message-container {
  display: inline-block;
  max-width: 60%;
  padding: 10px;
  border-radius: 10px;
}

.user-message {
  background-color: #dcf8c6;
  align-self: flex-start;
}

.bot-message {
  background-color: #e6e6e6;
  align-self: flex-end;
}

.message-content {
  margin: 0;
}
